.header {
  width: 100%;
  background-color: #C4C4C4;
}

.header__container {
  width: 80%;
  margin: 0 auto;
  padding: 24px 0;
  text-align: left;
}

.header__link {
  background-color: #9E9E9E;
  min-width: 205px;
  min-height: 92.45px;
  display: inline-block;
  color: black;
  text-decoration: none;
  font-weight: 400;
  font-size: 36px;
  line-height: calc(42 / 36);
}
